body {
  min-height: 90vh;
  background: linear-gradient(#f7ca18 26vh, white 0%);
  background-repeat: no-repeat;
}
.container {
  margin: 0px;
  padding: 0px;
  margin-bottom: 3vh;
  margin-top: 0.3vh;
  max-width: 100vw;
}
.container .logo {
  display: flex;
  text-align: left;
  margin-left: 1vw;
  font-size: 1.4vw;
  letter-spacing: 0.2ch;
  font-family: "Holtwood One SC", serif;
}
.container .logo p {
  margin: 0px;
  display: flex;
  align-items: flex-end;
}
.container .searchTerm {
  width: 30vw;
  height: 4vh;
}
.container .searchButton {
  border: none;
  height: 4vh;
  width: 2vw;
  background: powderblue;
}
.container .searchButton .fa-search {
  color: white;
}
.container .searchButton:hover {
  background: yellow;
}
.container .searchButton:hover .fa-search {
  color: black;
}
.movies-container {
  width: 60vw;
  margin: 0 auto;
}
.movies-container .movieImgs-container {
  background: white;
  height: 75.9vh;
  overflow: hidden;
  overflow-y: scroll;
  border: 0.1vw solid black;
}
.movies-container .movieImg-container {
  padding: 0;
}
.movies-container .movieImg-container .media {
  width: 100%;
  height: 100%;
  max-height: 44.24vh;
}
.movies-container .movieImg-container .movieImg-missing {
  display: flex;
  background: #aaaaaa;
  width: 100%;
  height: 100%;
  color: white;
  justify-content: center;
  align-items: center;
  font-size: 1.1vw;
}
.movies-container .movieImg-container .movieImg-missing p {
  width: 80%;
}
.movies-container ::-webkit-scrollbar {
  width: 1.2vw;
  border: 1px solid white;
}
.movies-container ::-webkit-scrollbar-track {
  background: white;
  box-shadow: inset 0 0 5px grey;
}
.movies-container ::-webkit-scrollbar-thumb {
  background: #f7d757;
}
.movieBtn-container {
  justify-content: center;
}
.singleMovie-container .movieImgs-container {
  margin-bottom: 1vh;
}
.singleImg-container .media {
  min-height: 66vh;
}
.singleImg-container .belowMovie-info {
  font-size: 1.3vw;
  padding: 0.3vw;
}
.mapedInfo-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.mapedInfo-container .mapped-info {
  padding: 0.1vw;
}
.moreInfo-container {
  padding: 0px;
}
.moreInfo-container .movie-overview {
  font-size: 1vw;
}
.moreInfo-container .moreInfo-local {
  font-size: 1.1vw;
  padding: 0.5vw 2vw 0.5vw;
  border-bottom: 0.1vh solid black;
}
.movie-info:hover {
  background: #f7ca18;
}
.movie-rating .progress {
  height: 3vh;
}
.movie-rating .progress .progress-bar {
  color: black;
  font-size: 1.2vw;
}
button.animated-button:link,
button.animated-button:visited {
  position: relative;
  display: block;
  margin: 30px auto 0;
  padding: 14px 15px;
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  letter-spacing: 0.08em;
  border-radius: 0;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}
button.animated-button:link:after,
button.animated-button:visited:after {
  position: absolute;
  height: 0%;
  left: 50%;
  top: 50%;
  width: 80%;
  z-index: -1;
  -webkit-transition: all 0.75s ease 0s;
  -moz-transition: all 0.75s ease 0s;
  -o-transition: all 0.75s ease 0s;
  transition: all 0.75s ease 0s;
}
button.animated-button:link:hover,
button.animated-button:visited:hover {
  color: #fff;
  text-shadow: none;
}
button.animated-button:link:hover:after,
button.animated-button:visited:hover:after {
  height: 450%;
}
