button.animated-button.thar-three {
  color: black;
  cursor: pointer;
  width: 100%;
  height: 5vh;
  font-size: 2vh;
  border: 2px solid #f7ca18;
  transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
}
button.animated-button.thar-three:hover {
  background-color: transparent;
  text-shadow: nthree;
}
button.animated-button.thar-three:hover:before {
  left: 3%;
  right: auto;
  width: 93.5%;
}
button.animated-button.thar-three:before {
  display: block;
  position: absolute;
  top: 0;
  right: 3%;
  width: 95.5%;
  height: 100%;
  width: 0px;
  z-index: -1;
  content: "";
  background: #f7ca18;
  transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
}
.btn:focus {
  box-shadow: none;
}
.selected {
  background: #f7ca18;
}
.movies-container .pagination {
  justify-content: center;
  width: 100%;
  margin: 1vh 0 1vh;
}
.movies-container .pagination .page-link {
  width: 3vw;
  color: black;
}
.movies-container .pagination .page-link:hover {
  background: #f7ca18;
}
.movies-container .pagination .page-link:focus {
  box-shadow: none;
}
